@font-face {
  font-family: 'console';
  src: url('../font/clacon.ttf')  format('truetype')
}

.container {
  border: 4px solid black;
  font-size: 24px;
  font-family: console;
  width: 700px;
  height: 400px;
  position: absolute;
  left: 50%;
  top: 30%;

  /* 
    Yep!
  */
  transform: translate(-50%, -30%);
  padding: 20px;
}

#textContainer {
  overflow:auto;
  height: 400px;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
  text-align: left
}

#textContainer::-webkit-scrollbar {
  display: none;
}

.comp {
  position: absolute;
  height: 700px;
  left: 52%;
  transform: translate(-50%, -30%);
  top: 44%;
  z-index: -1;
}

.right {
  text-align: right;
}